@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mukta:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@700&display=swap);
.navbar-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid #555657;
    background-color: rgba(250, 250, 250, 0.9);
    position: fixed;
    z-index: 1;
}
.nav-holder{
    width: 80%;
    height: 50px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}
.logo-holder > h2{
    font-family: 'Quicksand', sans-serif;
    font-size: 18px;
    color:#555657 ;
}
.navigation > ul > li{
    display: inline-block;
    list-style-type: none;
    margin: 0px 10px;
    font-size:13px ;
    font-family: 'Nanum Gothic', sans-serif;
    cursor: pointer;
    color:#555657 ;
}
.navigation > ul > li:hover{
    color: aqua;
}
@media (max-width: 800px){
    .nav-holder{
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
        height: auto;

    }
    .logo-holder{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .logo-holder h2{
        font-size: 15px;
    }
    .logo-menu{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .hamburger-holder{
        height: 40px;
        width: 100%;
        background: url('/assets/menu.png') no-repeat 85% center;
        
    }
    .navigation ul{
        height: 0;
        overflow: hidden; 
        display: flex;
        flex-direction: column;
        align-items: center;
       

    }
    .navigation > ul > li{
        margin: 4px 0;
    }
    
    .navigation ul.open{
        height: auto; 
        
        
    }
    
}
*{
  padding: 0;
  margin: 0;
}
.all{
    display: flex;
    justify-content: center;
    align-items: center;
}
.mainCont-cont{
    border: 1px solid black;
    height: 80vh;
    width: 100%;
    background:  url('/assets/wallpaper1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
    

}
.mainCont-cont:after {
    content: ' ';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: linear-gradient(0deg, rgba(78,26,236,0.9) 0%, rgba(144,9,240,0.1) 100%);
}


.main-headline-cont{
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 60%;
}

.main-headline h1{
    color: #ffffff;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
    font-size: 3em;
    text-shadow: 2px 2px #000000;
}

.headline-desc p {
   color: #ffffff;
   font-family: 'Montserrat', sans-serif;
   font-size: 13px;
}
.button-one{
    width: 170px;
    height: 30px;
    margin-top: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(250, 123, 42);
    color: #ffffff;
    font-family: 'Quicksand', sans-serif;
    font-size: 13px;
    cursor: pointer;
}
.contact-cont{
    height: 150px;
    width: 250px;
    margin-left: 20px;
    border-top: 4px solid rgb(250, 123, 42);
    background-color: rgba(23, 18, 117, 0.4);
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 12px;
    border-top-left-radius: 2%;
    border-top-right-radius: 2%;
    
}
.contact-details-first{
    margin-left: 10px;
    margin-top: 15px;
    font-weight: bold;
    font-size: 14px;
}
.contact-details-one{
    margin-top: 18px;
    margin-left: 10px;
}
.contact-details{
    margin-top: 8px;
    margin-left: 10px;
}
.register-button-two{
    position: relative;
    top: 26px;
    left: 10px;
    font-size:9px;
    height: 25px;
    font-family: 'Quicksand', sans-serif;
    width: 100px;
}
@media (max-width: 480px){
    .main-headline-cont{
        margin: auto;
        width: 90%;
        
    }
    .contact-cont{
        margin-top: 50px;
        display: none;
    }
}
@media (max-width: 800px){
    .main-headline-cont{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        margin: auto;
    }
    .contact-cont{
        margin-top: 50px;
        display: none;
        
    }
}
.about-wrapper{
    width: 100%;
    background: rgb(219,219,219);
    background: linear-gradient(0deg, rgba(219,219,219,0.6867121848739496) 0%, rgba(236,233,238,0) 100%);
    height: auto;

}
.about-headline-wrapper{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
}
.about-headline-wrapper h3{
    font-size: 2em;
    font-family: 'Mukta', sans-serif;
    color:#555657 ;
}
.about-headline-wrapper p{
    font-family: 'Nanum Gothic', sans-serif;
    font-size: 15px;
}
.about-headline-wrapper p{
    margin-top: 10px;
}
.about-card-wrapper{

    height: 50%;
    width: 80%;
    margin: auto;
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.about-card{
    width: 25%;
    height: 200px;
    border-top: 5px solid rgb(65,57,204);
    border-top-left-radius: 3%;
    border-top-right-radius: 3%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    box-shadow: 1px 0px 44px -24px rgba(0,0,0,0.75);
   
}
.card-image-wrapper{
    background-color: rgb(65,57,204);
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;
}
.card-image{
    height: 20px;
    width: 20px;
}
.about-card-headline{
    font-size: 13px;
    font-family: 'Mukta', sans-serif;
    color:#000000;
    margin-top:10px ;
}
.about-card-desc{
    margin-top: 10px;
    width: 70%;
    font-size: 11px;
    text-align: center;
    font-family: 'Nanum Gothic', sans-serif;
    color: #555657;
}
.agent-button{
    text-align: center;
    width: 150px;
    margin: auto;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Mukta', sans-serif;
    background-color: rgb(65,57,204);
    color: #ffffff;
    cursor: pointer;
    font-size: 13px;
    margin-top: 20px;
    margin-bottom: 20px;
}
@media (max-width: 480px){
    .about-card-wrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100vh;
        width: 100%;
        background: rgb(219,219,219);
        background: radial-gradient(circle, rgba(219,219,219,0.6867121848739496) 0%, rgba(236,233,238,0) 100%);
    }
    .about-headline-wrapper h3{
        font-size: 20px;
        width: 80%;
    }
    .desc-one{
        margin-top: 35px;
    }
    .about-headline-wrapper p{
        font-family: 'Nanum Gothic', sans-serif;
        font-size: 10px;
        width: 80%;
        text-align: center;
    }
    .about-card{
        width: 60%;
        margin-top: 0px;
       
    }
    .agent-button{
        margin-top: 20px;
    }

}
.offers-wrapper{
    height: 100vh;
    width: 100%;
}
@media (max-width: 480px){

    .about-headline-wrapper{
        width: 100%;
        text-align: center;
    }
}
.listing-cards-wrapper{
    width: 60%;
    margin: auto;
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(400px, auto);
    grid-gap: 20px;
    gap: 20px;
}
.listing-card{
    display: flex;
    flex-direction: column;
    box-shadow: -8px 16px 27px -13px rgba(0,0,0,0.52);
    
    
}
.card-image-holder{
    height: 50%;
    background-image: url('/assets/houses/house-one.jpg');
    background-size: cover;
}
.card-info-holder{
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.card-info-holder p{
    margin-top: 14px;
    font-size: 12px;
    color: #a1a1a1
}
.card-info-holder h2 {
    font-family: 'Ubuntu', sans-serif;
    color:#404040;
    font-weight: bold;
    margin-top: 10px;
}
.card-button{
    width: 100px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(250, 123, 42);
    color: #ffffff;
    font-family: 'Quicksand', sans-serif;
    font-size: 11px;
    cursor: pointer;
    position: relative;
    top: -15px;
    left: 30%;
box-shadow: 2px 12px 11px -6px rgba(0,0,0,0.52);
}
#two{
    background-image: url('/assets/houses/house-two.jpg');
